import React from "react";
import { PrimaryButton } from "office-ui-fabric-react";

export default class Success extends React.Component {
  handleViewTask = () => {
    const { id, projectId } = this.props.location.state;
    const w = window.open("about:blank");
    w.location.href = `https://ticktick.com/webapp/#p/${projectId}/tasks/${id}`;
  };

  render() {
    return (
      <div className="container">
        <h1 className="primaryTitle">Successfully Added</h1>
        <div className="primaryDesc">Your email has been added as a task. You can view it in TickTick</div>
        <PrimaryButton style={{ marginTop: 30 }} onClick={this.handleViewTask}>
          View Task
        </PrimaryButton>
      </div>
    );
  }
}
