import React from "react";
import { PrimaryButton } from "office-ui-fabric-react";

// 引入SVG文件
const backSvg = require("../svg/back.svg");

export default class User extends React.Component {
  state = {
    name: this.props.location.state ? this.props.location.state.name : ""
  };

  signOut = () => {
    localStorage.clear();
    this.props.history.push("/");
  };
  openTicktick = () => {
    const w = window.open("about:blank");
    w.location.href = "https://ticktick.com/webapp/#p/inbox/tasks";
  };
  render() {
    const { name } = this.state;
    return (
      <div className="container">
        <div className="userTitle">
          <img onClick={() => this.props.history.push("/add")} src={backSvg} className="svg"></img>
          <h1 className="userDesc">My TickTick Account</h1>
        </div>
        <div className="userInfo">
          <p className="userSignAs">Sign in as</p>
          <p className="userName" style={{ marginTop: 10, minHeight: 19 }}>
            {name}
          </p>
        </div>
        <PrimaryButton style={{ marginTop: 20 }} onClick={this.openTicktick}>
          Open TickTick
        </PrimaryButton>
        <PrimaryButton className="disabled" style={{ marginTop: 10 }} onClick={this.signOut}>
          Sign Out
        </PrimaryButton>
      </div>
    );
  }
}
