import React, { createRef } from "react";
import { PrimaryButton, TextField, Dropdown } from "office-ui-fabric-react";
import { addTask, getProjects, getUserInfo } from "../service";
import RelatedSelectMenu from "../components/Related";
import Timepicker from "../components/timepicker";
import $ from "jquery";
import moment from "moment";
import Calendar from "react-calendar";

// 引入SVG文件
const userSvg = require("../svg/user.svg");
const backSvg = require("../svg/go-back.svg");

export default class Add extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      title: "",
      content: "",
      projectId: "inbox",
      priority: 0,
      isShowTimeCard: false,
      hourDate: "",
      dayDate: "",
      username: "",
      lists: [
        {
          id: "inbox",
          label: "Inbox"
        }
      ]
    };
    this.dueDateRef = createRef();
    this.timePickerRef = createRef();
    this.chooseTimeRef = createRef();
  }

  handleAddTask = () => {
    // 2020-08-12T10:38:11.643+0000
    const { hourDate, dayDate, title, content, projectId, priority } = this.state;
    const task = {
      startDate: moment(dayDate + hourDate, "YYYY-MM-DDHH:mm")
        .utc()
        .format("YYYY-MM-DDTHH:mm:ss.SSSZZ"),
      title,
      content,
      projectId,
      priority,
      allDay: false
    };
    addTask(task).then(res => {
      const model = res.data;
      const id = model.id;
      const projectId = model.projectId;
      this.props.history.push({ pathname: "/success", state: { id, projectId } });
    });
  };

  componentDidMount() {
    const title = Office.context.mailbox.item.normalizedSubject;

    const dueDate = moment(Office.context.mailbox.item.dateTimeCreated);
    const dayDate = dueDate.format("YYYY-MM-DD");
    const hourDate = dueDate.format("HH:mm");

    Office.context.mailbox.item.body.getAsync("text", result => {
      const content = result.value.trim().replace(/(\n)+/g, "\n");
      this.setState({ title, dayDate, hourDate, content });
    });

    $("body").on("click", e => {
      if (!$(e.target).closest(this.chooseTimeRef.current).length) {
        this.timePickerRef.current && this.timePickerRef.current.hideWidget();
      }
    });

    getUserInfo().then(res => {
      const user = res.data;
      this.setState({
        username: user.name
      });
    });

    getProjects().then(res => {
      if (res.status === 200) {
        const lists = res.data;
        this.setState({ lists: lists.filter(list => list.id) });
      }
    });
  }

  componentWillUnmount() {
    $("body").off("click");
  }

  onChange = (type, event) => {
    switch (type) {
      case "title": {
        this.setState({
          title: event.target.value
        });
        break;
      }
      case "content": {
        this.setState({
          content: event.target.value
        });
        break;
      }
      default:
        break;
    }
  };

  onDateChange = date => this.setState({ dayDate: moment(date).format("YYYY-MM-DD") });

  onTimeChange = date => {
    this.setState({ hourDate: date.value });
  };

  clearTime = () => {
    this.setState({
      isShowTimeCard: false,
      dayDate: "",
      hourDate: ""
    });
  };

  confirmTime = () => {
    this.setState({
      isShowTimeCard: false
    });
  };

  onPriorityChange = (event, option, index) => {
    this.setState({ priority: option.key });
  };

  onProjectChange = (event, option, index) => {
    this.setState({ projectId: option.key });
  };

  getListOptions = () => {
    const { lists } = this.state;
    return lists.map(list => {
      return {
        key: list.id,
        text: list.label
      };
    });
  };
  getPriorityOptions = () => {
    return [
      { key: 0, text: "None Priority" },
      { key: 1, text: "Low Priority" },
      { key: 3, text: "Medium Priority" },
      { key: 5, text: "High Priority" }
    ];
  };

  renderNextLabel = () => <img src={backSvg} className="next_label" />;

  renderPrevLabel = () => <img src={backSvg} className="prev_label" />;

  render() {
    const { title, hourDate, dayDate, content, priority, projectId, isShowTimeCard, username } = this.state;
    return (
      <div className="container">
        <div className="addTitle">
          <p>Add email as a task</p>
          <img
            onClick={() => this.props.history.push({ pathname: "/user", state: { name: username } })}
            src={userSvg}
            className="svg"
          ></img>
        </div>
        <div className="taskProperty" ref={this.dueDateRef}>
          <TextField
            label="Due Date"
            iconProps={{ iconName: "Calendar" }}
            onClick={() => {
              this.setState({
                isShowTimeCard: !isShowTimeCard
              });
            }}
            readOnly
            value={`${dayDate} ${hourDate}`}
          />
        </div>
        {isShowTimeCard && (
          <RelatedSelectMenu
            hideMenu={() => {
              this.setState({
                isShowTimeCard: false
              });
            }}
            relativeRef={this.dueDateRef}
          >
            <div>
              <div className="calendar-wrapper">
                <Calendar
                  locale="en-US"
                  minDetail="month"
                  maxDatail="month"
                  next2Label={null}
                  prev2Label={null}
                  nextLabel={this.renderNextLabel()}
                  prevLabel={this.renderPrevLabel()}
                  formatShortWeekday={(locale, date) => moment(date).format("dd")}
                  value={moment(dayDate || undefined).toDate()}
                  onChange={this.onDateChange}
                  showFixedNumberOfWeeks
                />
              </div>
              <div ref={this.chooseTimeRef} className="timepicker">
                <Timepicker
                  ref={this.timePickerRef}
                  hourDifference={1}
                  minuteDifference={30}
                  extraClassName="input-sml"
                  hasWidget
                  placeholder="Set Time"
                  defaultTime={hourDate}
                  showMeridiem={false}
                  timeFormat=" "
                  onAwake={() => this.setState({ hourDate: hourDate || moment().format("HH:mm") })}
                  onChangeTime={this.onTimeChange}
                />
                <span className="delete" onClick={() => this.setState({ hourDate: "" })}></span>
              </div>
              <div className="date-btns">
                <button onClick={this.clearTime}>Clear</button>
                <button className="date-btn" onClick={this.confirmTime}>
                  OK
                </button>
              </div>
            </div>
          </RelatedSelectMenu>
        )}
        <div className="taskProperty">
          <TextField label="Task Name" className="input" value={title} onChange={event => onChange("title", event)} />
        </div>
        <div className="taskProperty">
          <TextField
            label="Task Content"
            className="input"
            value={content}
            onChange={event => onChange("content", event)}
            multiline
            rows={7}
          />
        </div>
        <div className="taskProperty">
          <Dropdown
            responsiveMode="small"
            defaultSelectedKey={projectId}
            label="List"
            options={this.getListOptions()}
            onChange={this.onProjectChange}
          />
        </div>
        <div className="taskProperty">
          <Dropdown
            responsiveMode="small"
            defaultSelectedKey={priority}
            label="Priority"
            options={this.getPriorityOptions()}
            onChange={this.onPriorityChange}
          />
        </div>
        <PrimaryButton className="addBtn" onClick={this.handleAddTask}>
          Add Task
        </PrimaryButton>
      </div>
    );
  }
}
