import React from "react";
import cn from "classnames";
import $ from "jquery";
import _ from "underscore";
import moment from "moment-timezone";

class Timepicker extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      showWidget: false,
      value: props.defaultTime
    };
    this.compatibleBrowser =
      navigator.userAgent.toLowerCase().match(/safari|trident|msie 10\.0|edge/) !== null &&
      navigator.userAgent.toLowerCase().match(/chrome/) === null;
    this.highlightedUnit = "";
    this.meridianPosition = props.timeFormat.length + (props.showSeconds ? 8 : 5);
    this.handleDifference();
    this.highlightUnitAndShowWidget = this.highlightUnitAndShowWidget.bind(this);
    this.highlightUnit = this.highlightUnit.bind(this);
    this.elementKeydown = this.elementKeydown.bind(this);
    this.onChangeTime = this.onChangeTime.bind(this);
    this.showWidget = this.showWidget.bind(this);
    this.hideWidget = this.hideWidget.bind(this);
    this.updateFromElementVal = this.updateFromElementVal.bind(this);
    this.handleWheel = this.handleWheel.bind(this);
    this.blurTimePicker = this.blurTimePicker.bind(this);
    this.widgetRef = React.createRef();
    this.triggerRef = React.createRef();
    this.wrapperRef = React.createRef();
  }

  // eslint-disable-next-line camelcase
  UNSAFE_componentWillReceiveProps(nextProps) {
    const { defaultTime } = this.props;
    if (nextProps.defaultTime !== defaultTime) {
      this.setState(
        {
          value: nextProps.defaultTime
        }
        // () => {
        //   this.scrollToRightWidgetPos();
        // }
      );
    }
  }

  // @maoq 前面keydown已经处理过了这里不再重复
  // 主要是为了避免React报错
  onChangeTime() {}

  getTime(original) {
    return this.formatTime(this.hour, this.minute, this.second, this.meridian, original);
  }

  getCursorPosition() {
    const $input = this.triggerRef.current;
    if (document.selection) {
      // IE fix
      $input.focus();
      const sel = document.selection.createRange();
      const selLen = document.selection.createRange().text.length;
      sel.moveStart("character", -$input.value.length);
      return sel.text.length - selLen;
    } else if ("selectionStart" in $input) {
      // Standard-compliant browsers
      return $input.selectionStart;
    }
    return null;
  }

  setTime(time) {
    let timeArray;
    const { showMeridiem, timeFormat, showSeconds } = this.props;
    if (showMeridiem) {
      const reg = new RegExp("([ 0-9:]+)" + timeFormat + "([ap]m)?", "i");
      const matched = time.match(reg);
      if (!matched) return;
      timeArray = matched[1].split(":");
      this.meridian = matched[2];
    } else {
      timeArray = time.split(":");
      // @summer set meridian when !showMeridiem
      // if meridian not set, formatTime function will goes wrong
      this.meridian = moment(time, "HH:mm")
        .locale("en")
        .format("A");
    }
    this.hour = parseInt(timeArray[0], 10);
    this.minute = parseInt(timeArray[1], 10);
    this.second = parseInt(timeArray[2], 10);

    if (Number.isNaN(this.hour)) {
      this.hour = 0;
    }
    if (Number.isNaN(this.minute)) {
      this.minute = 0;
    }
    if (Number.isNaN(this.second)) {
      this.second = 0;
    }

    if (showMeridiem) {
      if (this.hour > 12) {
        // @summer Modulo hour when it's exceeded
        // set meridian to PM
        this.hour %= 12;
        this.meridian = "PM";
      } else if (this.hour < 1) {
        this.hour = 12;
      }

      if (this.meridian === "am" || this.meridian === "a") {
        this.meridian = "AM";
      } else if (this.meridian === "pm" || this.meridian === "p") {
        this.meridian = "PM";
      }

      if (this.meridian !== "AM" && this.meridian !== "PM") {
        this.meridian = "AM";
      }
    } else {
      if (this.hour >= 24) {
        // @summer Modulo hour when it's exceeded
        // set meridian to PM
        this.hour %= 24;
        this.meridian = "PM";
      } else if (this.hour < 0) {
        this.hour = 0;
        this.meridian = "AM";
      }
    }

    if (this.minute < 0) {
      this.minute = 0;
    } else if (this.minute >= 60) {
      this.minute = 59;
    }

    if (showSeconds) {
      if (Number.isNaN(this.second)) {
        this.second = 0;
      } else if (this.second < 0) {
        this.second = 0;
      } else if (this.second >= 60) {
        this.second = 59;
      }
    }
    this.update();
  }

  formatWidgetTime(minutes) {
    const { showMeridiem, timeFormat } = this.props;
    if (minutes < 0) {
      minutes += 24 * 60;
    } else if (minutes >= 24 * 60) {
      minutes -= 24 * 60;
    }
    // [时,分]
    const parts = [];
    parts[1] = this.formatUnit(minutes % 60);
    if (!showMeridiem) {
      parts[0] = this.formatUnit(Math.floor(minutes / 60));
    } else {
      parts[0] = Math.floor(minutes / 60);
      parts[1] += timeFormat + (parts[0] >= 12 ? "PM" : "AM");
      // 00:00 显示为 12:00am
      parts[0] = parts[0] || 12;
      parts[0] = parts[0] > 12 ? this.formatUnit(parts[0] % 12) : this.formatUnit(parts[0]);
    }
    return parts.join(":");
  }

  formatUnit(num) {
    return num < 10 ? "0" + num : num;
  }

  handleDifference() {
    var { minuteDifference, hourDifference } = this.props;
    minuteDifference = parseInt(minuteDifference, 10);
    hourDifference = parseInt(hourDifference, 10);
    if (hourDifference > 24) {
      hourDifference %= 24;
    }
    if (minuteDifference > 60) {
      minuteDifference %= 60;
    }
    this.minuteDifference = minuteDifference;
    this.hourDifference = hourDifference;
  }

  highlightUnitAndShowWidget(e) {
    const { onAwake } = this.props;
    const { showWidget } = this.state;
    const highlightedUnit = this.highlightedUnit;
    this.highlightUnit();
    if (highlightedUnit !== this.highlightedUnit) {
      this.updateFromElementVal();
    }
    onAwake && onAwake();
    if (highlightedUnit === "meridian" && this.highlightedUnit === "meridian") {
      this.toggleMeridian();
    }
    if (showWidget) {
      this.hideWidget();
    } else {
      this.showWidget();
    }
  }

  highlightUnit() {
    const { showSeconds } = this.props;
    const position = this.getCursorPosition();
    if (position >= 0 && position <= 2) {
      this.highlightHour();
    } else if (position >= 3 && position <= 5) {
      this.highlightMinute();
    } else if (position >= 6 && position <= 8 && showSeconds) {
      this.highlightSecond();
    } else if (position >= this.meridianPosition && position <= this.meridianPosition + 2) {
      this.highlightMeridian();
    }
  }

  /* fix blink when change value by key or mouse */
  _highlightHour() {
    var $element = $(this.triggerRef.current).get(0);
    this.highlightedUnit = "hour";
    if ($element.setSelectionRange) {
      $element.setSelectionRange(0, 2);
    }
  }

  _highlightMinute() {
    var $element = $(this.triggerRef.current).get(0);
    this.highlightedUnit = "minute";
    if ($element.setSelectionRange) {
      $element.setSelectionRange(3, 5);
    }
  }

  _highlightSecond() {
    var $element = $(this.triggerRef.current).get(0);
    this.highlightedUnit = "second";
    if ($element.setSelectionRange) {
      $element.setSelectionRange(6, 8);
    }
  }

  _highlightMeridian() {
    const { showSeconds } = this.props;
    const $element = $(this.triggerRef.current).get(0);
    this.highlightedUnit = "meridian";
    if ($element.setSelectionRange) {
      if (showSeconds) {
        $element.setSelectionRange(9, 11);
      } else {
        $element.setSelectionRange(this.meridianPosition, this.meridianPosition + 2);
      }
    }
  }

  _highlightCurrentUnit() {
    switch (this.highlightedUnit) {
      case "hour":
        this._highlightHour();
        break;
      case "minute":
        this._highlightMinute();
        break;
      case "second":
        this._highlightSecond();
        break;
      case "meridian":
        this._highlightMeridian();
        break;
      default:
        break;
    }
  }

  /* fix blink end */

  highlightHour() {
    const $input = this.triggerRef.current;
    this.highlightedUnit = "hour";
    if ($input.setSelectionRange) {
      setTimeout(() => {
        $input.setSelectionRange(0, 2);
      });
    }
  }

  highlightMinute() {
    const $input = this.triggerRef.current;
    this.highlightedUnit = "minute";
    if ($input.setSelectionRange) {
      setTimeout(() => {
        $input.setSelectionRange(3, 5);
      });
    }
  }

  highlightSecond() {
    const $input = this.triggerRef.current;
    this.highlightedUnit = "second";
    if ($input.setSelectionRange) {
      setTimeout(() => {
        $input.setSelectionRange(6, 8);
      });
    }
  }

  highlightMeridian() {
    const meridianPosition = this.meridianPosition;
    const $input = this.triggerRef.current;
    this.highlightedUnit = "meridian";
    if ($input.setSelectionRange) {
      setTimeout(() => {
        $input.setSelectionRange(meridianPosition, meridianPosition + 2);
      });
    }
  }

  updateFromElementVal() {
    const val = this.triggerRef.current.value;
    if (val) {
      this.setTime(val);
    }
  }

  blurTimePicker() {
    this.updateFromElementVal();
    const { onBlur } = this.props;
    onBlur &&
      onBlur({
        value: this.getTime(true),
        hours: this.hour,
        minutes: this.minute,
        seconds: this.second,
        meridian: this.meridian
      });
  }

  update(cb) {
    const { onChangeTime } = this.props;
    onChangeTime &&
      onChangeTime({
        value: this.getTime(true),
        hours: this.hour,
        minutes: this.minute,
        seconds: this.second,
        meridian: this.meridian
      });
    this.setState({ value: this.getTime() }, cb);
  }

  updateWidget() {
    if (this.$widget === false) {
      return;
    }
    const { showInputs, showSeconds, showMeridiem } = this.props;
    const hour = this.hour < 10 ? "0" + this.hour : this.hour;
    const minute = this.minute < 10 ? "0" + this.minute : this.minute;
    const second = this.second < 10 ? "0" + this.second : this.second;

    if (showInputs) {
      this.$widget.find("input.timepicker-hour").val(hour);
      this.$widget.find("input.timepicker-minute").val(minute);

      if (showSeconds) {
        this.$widget.find("input.timepicker-second").val(second);
      }
      if (showMeridiem) {
        this.$widget.find("input.timepicker-meridian").val(this.meridian);
      }
    } else {
      this.$widget.find("span.timepicker-hour").text(hour);
      this.$widget.find("span.timepicker-minute").text(minute);

      if (showSeconds) {
        this.$widget.find("span.timepicker-second").text(second);
      }
      if (showMeridiem) {
        this.$widget.find("span.timepicker-meridian").text(this.meridian);
      }
    }
  }

  formatTime(hour, minute, second, meridian, original) {
    const { showSeconds, timeFormat, showMeridiem } = this.props;
    hour = hour < 10 ? "0" + hour : hour;
    minute = minute < 10 ? "0" + minute : minute;
    second = second < 10 ? "0" + second : second;
    // @summer format time to 24hour when !showMeridiem
    let time = hour + ":" + minute + (showSeconds ? ":" + second : "") + (original ? " " : timeFormat) + meridian;
    if (!showMeridiem) {
      time = moment(time, "HH:mm")
        .locale("en")
        .format("HH:mm");
    }
    return time;
  }

  toggleMeridian(needPreventHide, cb) {
    if (!needPreventHide) {
      this.hideWidget();
    }
    this.meridian = this.meridian === "AM" ? "PM" : "AM";
    this.update(cb);
  }

  showWidget() {
    const { hasWidget } = this.props;
    if (hasWidget) {
      this.setState(
        {
          showWidget: true
        },
        () => {
          this.fixPosition();
          // this.scrollToRightWidgetPos();
        }
      );
    }
  }

  fixPosition() {
    const defaultHeight = 300;
    const triggerEl = this.triggerRef.current;
    const triggerRect = triggerEl.getBoundingClientRect();
    const triggerTop = triggerRect.top;
    const triggerBottom = triggerRect.bottom;
    const triggerHeight = triggerRect.height;
    const bottomDistance = window.innerHeight - triggerBottom;
    const topDistance = triggerTop;
    const widgetHeight = defaultHeight;
    const paddingHeight = 4;
    if (bottomDistance > widgetHeight + paddingHeight) {
      this.widgetRef.current.style.height = widgetHeight + "px";
      this.widgetRef.current.style.top = triggerHeight + paddingHeight + "px";
    } else {
      if (topDistance > widgetHeight + paddingHeight) {
        this.widgetRef.current.style.height = widgetHeight + "px";
        this.widgetRef.current.style.top = -widgetHeight - paddingHeight + "px";
      } else {
        this.widgetRef.current.style.top = triggerHeight + "px";
        this.widgetRef.current.style.height = bottomDistance - paddingHeight + "px";
      }
    }
  }

  // scrollToRightWidgetPos() {
  //   const { value } = this.state;
  //   if (this.widgetRef.current) {
  //     var time = preferences.getHour(value);
  //     if (time === "Invalid date") {
  //       time = "00:00";
  //     }
  //     time = time.split(":");
  //     var hours = parseInt(time[0], 10);
  //     var minutes = parseInt(time[1], 10);
  //     var steps = hours * 2 + minutes / 30;
  //     var newIndex = Math.ceil(steps);
  //     if (this.wrapperRef.current) {
  //       $(this.wrapperRef.current).find(".antiscroll-inner")[0].scrollTop = newIndex * 36;
  //     }
  //   }
  // }

  hideWidget() {
    const { hasWidget } = this.props;
    if (hasWidget) {
      this.setState({
        showWidget: false
      });
    }
  }

  handleWheel(e) {
    let type = null;
    const el = this.triggerRef.current;
    if (e.nativeEvent.deltaY <= 0) {
      // 上
      if (el.scrollTop <= 0) {
        type = "decrement";
      }
    } else {
      // 下
      if (el.scrollTop + el.clientHeight >= el.scrollHeight) {
        type = "increment";
      }
    }
    if (type) {
      switch (this.highlightedUnit) {
        case "hour":
          this[type + "Hour"](null, () => {
            this._highlightHour();
          });

          break;
        case "minute":
          this[type + "Minute"](null, () => {
            this._highlightMinute();
          });
          break;
        case "second":
          this[type + "Second"](() => {
            this._highlightMinute();
          });
          break;
        case "meridian":
          this.toggleMeridian(false, () => {
            this._highlightMeridian();
          });
          break;
        default:
          break;
      }
    }
  }

  elementKeydown(e) {
    const { showMeridiem, isOpen, template } = this.props;
    const charCode = e.which ? e.which : e.keyCode;
    switch (charCode) {
      case 9: // tab
        this.updateFromElementVal();
        switch (this.highlightedUnit) {
          case "hour":
          case "minute":
          case "meridian":
            e.preventDefault();
            this.highlightNextUnit();
            break;
          case "second":
            if (showMeridiem) {
              e.preventDefault();
              this.highlightNextUnit();
            }
            break;
          default:
            break;
        }
        break;
      case 27: // escape
        this.hideWidget();
        this.updateFromElementVal();
        break;
      case 37: // left arrow
        e.preventDefault();
        this.highlightPrevUnit();
        this.updateFromElementVal();
        break;
      case 38: // up arrow
        e.preventDefault();
        if (this.highlightedUnit === "meridian" || !isOpen || template !== "dropdown") {
          switch (this.highlightedUnit) {
            case "hour":
              this.decrementHour(null, () => {
                this._highlightHour();
              });
              break;
            case "minute":
              this.decrementMinute(null, () => {
                this._highlightMinute();
              });
              break;
            case "second":
              this.decrementSecond(() => {
                this._highlightSecond();
              });
              break;
            case "meridian":
              this.toggleMeridian(false, () => {
                this._highlightMeridian();
              });
              break;
            default:
              break;
          }
        }
        break;
      case 39: // right arrow
        e.preventDefault();
        this.updateFromElementVal();
        this.highlightNextUnit();
        break;
      case 40: // down arrow
        e.preventDefault();
        if (this.highlightedUnit === "meridian" || !isOpen || template !== "dropdown") {
          switch (this.highlightedUnit) {
            case "hour":
              this.incrementHour(null, () => {
                this._highlightHour();
              });
              break;
            case "minute":
              this.incrementMinute(null, () => {
                this._highlightMinute();
              });
              break;
            case "second":
              this.incrementSecond(() => {
                this._highlightSecond();
              });
              break;
            case "meridian":
              this.toggleMeridian(false, () => {
                this._highlightMeridian();
              });
              break;
            default:
              break;
          }
        }
        break;
      case 13: // enter
        if (isOpen && template === "dropdown") {
          $(this.triggerRef.current).blur();
        } else {
          this.hideWidget();
          $(this.triggerRef.current).blur();
        }
        break;
      default: {
        // 对safari, edge下的中文输入法，e.preventDefault()没有作用
        e.preventDefault();
        const key = parseInt(e.key, 10);
        // 这里对中文输入法输入非数字做一下控制
        if (charCode === 229 && Number.isNaN(key)) {
          return;
        }
        const cursorPosition = this.getCursorPosition();
        // 其他键位处理
        if (charCode === 229 && this.compatibleBrowser) {
          this.updateCompatibleInput();
        } else if (cursorPosition <= 1 || (cursorPosition > 2 && cursorPosition < 5)) {
          if ((e.keyCode >= 48 && e.keyCode <= 57) || (e.keyCode >= 96 && e.keyCode <= 105) || (key >= 0 && key <= 9)) {
            this.updateInputNumber(e.keyCode % 48);
            this.hideWidget();
          }
        } else if (cursorPosition === this.meridianPosition) {
          // keycode['numpad_1'] || keycode['f1'] || keycode['a'] || keycode['p'])
          if ([97, 112, 65, 80].includes(e.keyCode)) {
            this.toggleMeridian(false, () => {
              this._highlightMeridian();
            });
          }
        } else if (cursorPosition === this.meridianPosition + 1) {
          // keycode['subtract'] || keycode['m']
          if ([109, 77].includes(e.keyCode)) {
            this.toggleMeridian(false, () => {
              this._highlightMeridian();
            });
          }
        }
        break;
      }
    }
  }

  highlightPrevUnit() {
    const { showSeconds } = this.props;
    switch (this.highlightedUnit) {
      case "hour":
        this.highlightMeridian();
        break;
      case "minute":
        this.highlightHour();
        break;
      case "second":
        this.highlightMinute();
        break;
      case "meridian":
        if (showSeconds) {
          this.highlightSecond();
        } else {
          this.highlightMinute();
        }
        break;
      default:
        break;
    }
  }

  highlightNextUnit() {
    const { showSeconds, showMeridiem } = this.props;
    switch (this.highlightedUnit) {
      case "hour":
        this.highlightMinute();
        break;
      case "minute":
        if (showSeconds) {
          this.highlightSecond();
        } else if (showMeridiem) {
          this.highlightMeridian();
        } else {
          this.highlightHour();
        }
        break;
      case "second":
        if (showMeridiem) {
          this.highlightMeridian();
        } else {
          this.highlightHour();
        }
        break;
      case "meridian":
        this.highlightHour();
        break;
      default:
        break;
    }
  }

  // 存在hours，优先处理hours，不存在hours，则以props传递进来的为准
  decrementHour(hours, cb) {
    const { showMeridiem } = this.props;
    hours = hours || this.hourDifference;
    if (hours) {
      // 有单独的hourDecrement的减量
      if (showMeridiem) {
        var needToggleMeridian = false;
        this.hour -= hours;
        if (this.hour < 1) {
          this.hour += 12;
        }

        if (this.hour === 11) {
          needToggleMeridian = !needToggleMeridian;
        }

        if (needToggleMeridian) {
          this.toggleMeridian(true);
        }
      } else {
        this.hour -= hours;
        if (this.hour < 0) {
          this.hour += 24;
        }
      }
    } else {
      // 没有单独的增减量，则以分钟为准
      this.decrementMinute();
    }
    this.update(cb);
  }

  decrementMinute(minutes, cb) {
    minutes = minutes || this.minuteDifference;
    this.minute -= minutes;
    if (this.minute < 0) {
      this.minute += 60;
      this.decrementHour(1);
    }

    this.update(cb);
  }

  decrementSecond(cb) {
    var newVal = this.second - this.secondStep;

    if (newVal < 0) {
      this.second = newVal + 60;
      this.decrementMinute(1);
    } else {
      this.second = newVal;
    }
    this.update(cb);
  }

  incrementHour(hours, cb) {
    const { showMeridiem } = this.props;
    hours = hours || this.hourDifference;
    if (hours) {
      // 有单独的hourDecrement的减量
      if (showMeridiem) {
        var needToggleMeridian = false;
        this.hour += hours;
        if (this.hour > 12) {
          this.hour -= 12;
        }

        if (this.hour === 12) {
          needToggleMeridian = !needToggleMeridian;
        }
        if (needToggleMeridian) {
          this.toggleMeridian(true);
        }
      } else {
        this.hour += hours;
        if (this.hour > 23) {
          this.hour -= 24;
        }
      }
    } else {
      // 没有单独的增减量，则以分钟为准
      this.incrementMinute();
    }
    this.update(cb);
  }

  incrementMinute(minutes, cb) {
    minutes = minutes || this.minuteDifference;
    this.minute += minutes;
    if (this.minute > 59) {
      this.minute -= 60;
      this.incrementHour(1);
    }

    this.update(cb);
  }

  incrementSecond(cb) {
    const newVal = this.second + this.secondStep - (this.second % this.secondStep);

    if (newVal > 59) {
      this.second = newVal - 60;
      this.incrementMinute(true);
    } else {
      this.second = newVal;
    }
    this.update(cb);
  }

  updateCompatibleInput() {
    const { showMeridiem, timeFormat } = this.props;
    const text = $(this.triggerRef.current).val();
    let time = showMeridiem ? text.slice(0, text.lastIndexOf(timeFormat)) : text;
    const index = this.highlightedUnit === "hour" ? 0 : 1;
    time = time.split(":");

    time[index] = parseInt(time[index], 10);
    this[this.highlightedUnit] = time[index];

    if (time[index] >= 10) {
      this.highlightNextUnit();
      this.updateFromElementVal();
    }
  }

  // @curly 数字输入处理，输入一位数字显示如' 1'，输入两位数字跳转至下个单位
  updateInputNumber(number) {
    const { showMeridiem, timeFormat } = this.props;
    let text = $(this.triggerRef.current).val();
    let time = showMeridiem ? text.slice(0, text.lastIndexOf(timeFormat)) : text;
    const median = text.replace(time, "");
    const index = this.highlightedUnit === "hour" ? 0 : 1;
    time = time.split(":");
    const isEditing = time[index].indexOf(" ") === 0;
    time[index] = parseInt(time[index], 10);
    if (time[index] < 10 && isEditing) {
      // 完整输入，跳到下一位并进行校验
      time[index] = time[index] * 10 + number;
      this[this.highlightedUnit] = time[index];
      text = time.join(":").concat(median);
      this.setState(
        {
          value: text
        },
        () => {
          this.highlightNextUnit();
          this.updateFromElementVal();
        }
      );
    } else {
      this[this.highlightedUnit] = time[index];
      time[index] = " " + number;
      text = time.join(":").concat(median);
      this.setState(
        {
          value: text
        },
        () => {
          this._highlightCurrentUnit();
        }
      );
    }
  }

  clearTime() {
    this.triggerRef.current.value = "";
  }

  renderWidget() {
    const { widgetFooterComponent } = this.props;
    const times = [];
    var minutes = 0;
    const range = 60 * 24;
    const step = 30;
    while (minutes < range) {
      const text = this.formatWidgetTime(minutes);
      times.push({
        text,
        isActive: text === this.getTime(true)
      });
      minutes += step;
    }
    return (
      <div className="timepicker-widget dropdown-menu open" ref={this.widgetRef}>
        <div className="timepicker-widget-content antiscroll-wrap" ref={this.wrapperRef}>
          <ul className="timepicker-quick-selector antiscroll-inner">
            {times.map(time => {
              return (
                <li
                  key={time.text}
                  className={cn("dropdown-menu-item", {
                    active: time.isActive
                  })}
                  onClick={() => {
                    this.setTime(time.text);
                    this.setState({
                      showWidget: false
                    });
                  }}
                >
                  <a>{time.text}</a>
                </li>
              );
            })}
          </ul>
        </div>
        <div className="timepicker-widget-footer">{widgetFooterComponent}</div>
      </div>
    );
  }

  render() {
    // @maoq defaultTime必须默认设一个'' 不能使undefined 否则React报错
    const { maxLength, placeholder, extraClassName } = this.props;
    const { showWidget } = this.state;
    var { value } = this.state;
    value = value || "";
    return (
      <>
        {showWidget && this.renderWidget()}
        <input
          ref={this.triggerRef}
          type="text"
          maxLength={maxLength}
          placeholder={placeholder}
          className={extraClassName}
          onClick={this.highlightUnitAndShowWidget}
          onKeyDown={this.elementKeydown}
          value={value}
          onChange={this.onChangeTime}
          onBlur={this.blurTimePicker}
          onWheel={this.handleWheel}
          tabIndex="-1"
        />
      </>
    );
  }
}

