  defaultTime: "",
  maxLength: 12,
  placeholder: "",
  extraClassName: "",
  isOpen: false,
  showSeconds: false,
  showInputs: false,
  showMeridiem: true,
  template: "dropdown",
  timeFormat: "  |  ",
  // 小时分钟的增减量，当小时增减量为0时，小时的增减量将以分钟增减量为准
  // 小时的增减量的取值为0-24,其中0是置为无效的数字取值
  // 分钟的增减量取值为0-60
  hourDifference: 1,
  minuteDifference: 15,
  hasWidget: false,
  widgetFooterComponent: null,
  onAwake: null,
  onBlur: null,
  onChangeTime: null
