import React from "react";
import { PrimaryButton } from "office-ui-fabric-react";
import { initAxiosInterceptors } from "../service/axios";

export default class SignIn extends React.Component {
  componentDidMount() {
    const token = localStorage.getItem("ot");
    if (token) {
      initAxiosInterceptors();
      this.props.history.push({ pathname: "/add" });
    }
  }

  dialogCallback = asyncResult => {
    if (asyncResult.status == "failed") {
      // In addition to general system errors, there are 3 specific errors for
      // displayDialogAsync that you can handle individually.
      switch (asyncResult.error.code) {
        case 12004:
          showNotification("Domain is not trusted");
          break;
        case 12005:
          showNotification("HTTPS is required");
          break;
        case 12007:
          showNotification("A dialog is already opened.");
          break;
        default:
          showNotification(asyncResult.error.message);
          break;
      }
    } else {
      this.dialog = asyncResult.value;
      this.dialog.addEventHandler(Office.EventType.DialogMessageReceived, this.messageHandler);
      this.dialog.addEventHandler(Office.EventType.DialogEventReceived, this.eventHandler);
    }
  };

  messageHandler = arg => {
    this.dialog.close();
    const messageFromLogin = JSON.parse(arg.message);
    if (messageFromLogin.status === "auth success") {
      initAxiosInterceptors();
      this.props.history.push({ pathname: "/add" });
    } else {
      this.props.history.push({ pathname: "/error" });
    }
  };

  eventHandler = arg => {
    this.props.history.push({ pathname: "/error" });
    switch (arg.error) {
      case 12002:
        showNotification("Cannot load URL, no such page or bad URL syntax.");
        break;
      case 12003:
        showNotification("HTTPS is required.");
        break;
      case 12006:
        showNotification("Dialog closed by user");
        break;
      default:
        showNotification("Undefined error in dialog window");
        break;
    }
  };

  handleSignIn = () => {
    Office.context.ui.displayDialogAsync(
      window.location.origin + "/login.html",
      { width: 50, height: 50 },
      this.dialogCallback
    );
  };

  learnMore = () => {
    const w = window.open("about:blank");
    w.location.href = "https://ticktick.com/home";
  };

  render() {
    return (
      <div className="container">
        <h1 className="primaryTitle">Sign in TickTick</h1>
        <div className="primaryDesc">In order to create new tasks, we will need access to your TickTick account.</div>
        <PrimaryButton className="signinBtn" onClick={this.handleSignIn}>
          Sign in
        </PrimaryButton>
        <PrimaryButton className="moreBtn disabled" onClick={this.learnMore}>
          Learn more about TickTick
        </PrimaryButton>
      </div>
    );
  }
}
