import { useRef, useEffect, useCallback } from "react";
import $ from "jquery";

export function useMenuValidClick(menuRef, maskRef, hideMenu) {
  useEffect(() => {
    function checkHideMenu(e) {
      if (
        e.target instanceof Node &&
        maskRef &&
        maskRef.current &&
        maskRef.current.contains(e.target) &&
        !(menuRef.current && menuRef.current.contains(e.target))
      ) {
        e.stopPropagation();
        e.preventDefault();
        hideMenu();
      }
    }
    const maskEl = maskRef ? maskRef.current : null;
    maskEl && $(maskEl).on("click", checkHideMenu);

    return () => {
      maskEl && $(maskEl).off("click", checkHideMenu);
    };
  }, [hideMenu, maskRef, menuRef]);
}

