import axios from "axios";

axios.defaults.headers.common["Content-Type"] = "application/json";
axios.defaults.timeout = 120000;

const commonErrorHandler = function(error) {
  const responseError = error.response;

  if (responseError) {
    switch (responseError.status) {
      case 401:
        dom.showStatus("Redirect to sign in...", 10000);
        setTimeout(function() {
          window.location.href = window.location.protocol + window.location.host + "/taskpane.html";
        }, 5000);
        break;
      default:
        break;
    }
  }
  return Promise.reject(error);
};

export const apiPub = axios.create({
  baseURL: "https://api.ticktick.com/open/v1",
  defaultInterceptors: true
});

export const initAxiosInterceptors = () => {
  const accessToken = localStorage.getItem("ot");
  apiPub.interceptors.request.use(
    config => {
      config.headers["Authorization"] = "Bearer " + accessToken;
      return config;
    },
    error => {
      console.log(error);
      Promise.reject(error);
    }
  );
  apiPub.interceptors.response.use(undefined, commonErrorHandler);
};
