import React, { useRef, useEffect, useState, useCallback } from "react";
import { Portal } from "react-portal";
import { useMenuValidClick, useMenuValidRightClick, useHookWithRefCallback } from "./hooks";
import $ from "jquery";

const fixPosition = (node, position) => {
  let { top, left, width } = position;
  if (!node) {
    return;
  }
  const { offsetHeight: menuHeight, offsetWidth: menuWidth } = node;

  if (top + menuHeight > window.innerHeight) {
    top -= menuHeight;
  }

  if (left + menuWidth > window.innerWidth) {
    left -= menuWidth;
  }

  $(node).css({
    top: top + "px",
    left: Math.max(left, 0) + "px",
    width: width * 0.9 + "px"
  });
};

function RelatedSelectMenu({ relativeRef, hideMenu, children }) {
  const maskRef = useRef(null);
  const [position, setPosition] = useState({ top: 0, left: 0, width: 0 });
  const setInitialPositon = useCallback(
    node => {
      if (node) {
        if (relativeRef && relativeRef.current) {
          const { top, left, height, width } = relativeRef.current.getBoundingClientRect();
          setPosition({ top: top + height, left, width });
        } else {
          setPosition({ top: 0, left: 0, width: 0 });
        }
      }
    },
    [setPosition]
  );
  const [menuRef, setMenuRef] = useHookWithRefCallback(setInitialPositon);

  const { top, left, width } = position;

  useMenuValidClick(menuRef, maskRef, hideMenu);
  useMenuValidRightClick(menuRef, maskRef, hideMenu);

  useEffect(() => {
    menuRef.current && fixPosition(menuRef.current, position);
  }, [position]);

  return (
    <Portal>
      <div className="menu-mask" ref={maskRef}>
        <div
          className="dropdown-menu"
          ref={setMenuRef}
          style={{
            top: top + "px",
            left: left + "px",
            width: width * 0.9 + "px"
          }}
        >
          {children}
        </div>
      </div>
    </Portal>
  );
}

export default RelatedSelectMenu;
